html {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

:root {
  --link-color: #00acdc;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  height: 40px;
  display: flex;
}

.navigation {
  align-items: center;
  display: flex;
}

.nav__list {
  justify-content: flex-end;
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  text-decoration: none;
}

.nav__link.is-active {
  color: #00acdc;
  margin-left: 0;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}
